.main_container {
  padding: 0 15px 0 15px;
  box-sizing: border-box;
  margin: 0 auto;
  background: #F5F5F5;
}

.title {
  color: #282882;
  font-family: Test Domaine Text;
  font-size: calc((100vw - 320px)/(1920 - 320) * (55 - 32) + 32px);
  font-style: normal;
  font-weight: 600;
  line-height: 120%; 
  margin: 0;
  max-width: 500px;
  text-align: center;
  margin: 0 auto;
  margin-bottom: calc((100vw - 320px)/(1920 - 320) * (62 - 48) + 48px);
}

.wrapper {
  max-width: 1148px;
  padding-bottom: 49px;
  /* display: flex; */
}

.slide {

}

.new_container {
  background-color: white;
  display: flex;
  border-radius: 20px;
  padding: 18px 18px 22px 26px;
  
}

.new_container_text_column {
  display: flex;
  flex-direction: column;
}

.new_date {
  color: #9D9D9D;
  font-family: Geometria;
  font-size: 11px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin: 0;
  margin-top: 29px;
  margin-bottom: 12px;

}

.new_title {
  color: #282882;
  font-family: Test Domaine Text;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 125%;
  margin: 0;
  margin-bottom: 24px;
}

.new_text {
  color: #9D9D9D;
  font-family: Geometria;
  font-size: 13px;
  font-style: italic;
  font-weight: 500;
  line-height: normal;
  max-width: 290px;
  max-height: 67px;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;  
  overflow: hidden;
}

.new_container_image_column {
  border-radius: 20px;
  overflow: hidden;
  width: 207px;
  height: 255px;
}

.new_image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}