.footer {
  background: #4E4E4E;
  color: #fff;
  padding: 24px 0 13px ;
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  gap: 46px;
  padding-left: calc((100vw - 320px)/(1920 - 320) * (240 - 16) + 16px);
  padding-right: calc((100vw - 320px)/(1920 - 320) * (240 - 0) + 0px);

  box-sizing: border-box;
  

}

.links_container {
  max-width: 969px;
  display: flex;
  flex: 1;
  justify-content: space-between;
  padding: 0;
  margin: 0;
  list-style: none;
  gap: 30px;
  
}

.links_container_columns {
  list-style: none;

}

.link_main {
  font-family: Test Domaine Text;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 70px;
  color: rgba(203, 203, 203, 1);
  text-decoration: none;
  position: relative;
  transition: color 0.3s ease; 

}

.link_main:hover {
  color: #EC5E43;

}

.icon {
  width: 24px;
  height: 24px;
  transition: transform 0.3s ease; 
  color: rgba(255, 255, 255, 1);
  

}


.link_main:hover .icon{
  transform: rotate(180deg);
}
.link_main_active {

}


.link_column {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
}

.link_minor {
  text-decoration: none;
  color: #DADADA;
  font-family: Mont;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 40px;
  transition: color 0.3s ease; 

}


.link_minor:hover {
  color: #EC5E43;
}

.logo_copyright_container {
  align-self: flex-end;
}

.copyright {
  color: #DADADA;
  font-family: Mont;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 40px; 
  text-align: right;
  margin: 0;
}


@media (max-width: 950px) {
  .footer {
    flex-direction: column;
  }
  .links_container {
    flex-direction: column; /* Изменение направления flex на колонку */
    align-items: flex-start; /* Выравнивание элементов в начало колонки */
  }

  .links_container_columns {
    margin-top: 10px; /* Добавьте отступ между столбцами */
  }

  /* Скрытие ссылок дополнительного уровня по умолчанию */
  .links_container_columns ul {
    display: none;
  }

  /* Отображение ссылок дополнительного уровня при наведении на ссылку основного уровня */
  .links_container_columns:hover ul {
    display: block;
  }

  .logo_copyright_container {
    align-self: flex-start;
  }
}