.header {
  /* padding: 44px 0; */
}

.header_wrapper {
  max-width: 1470px;
  margin: 0 auto;
  padding: 0 15px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.fff svg {
  width: calc((100vw - 320px)/(1920 - 320) * (325 - 217) + 217px);
  /* height: auto; */
}



.nav_container_hidden {
  display: none;
}

.nav_list {
  flex: 1;
  display: flex;
  gap: 40px;
  list-style: none;
  margin: 0;
  padding: 0;
}

.link {
  color: #282882;
  font-family: Mont;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 45.5px; /* 227.5% */
  transition: all .2s ease-out;
  position: relative;
  text-decoration: none;
}

.link:hover {
  color: #EC5E43;
}

.link::after {
  position: absolute;
  content: '';
  width: 0;
  height: 2px;
  background-color: red;
  bottom: -3px;
  left: 0;
  opacity: 0;
  transition: width .2s ease-in;
}


.link:hover:after {
  opacity: 1;
  width: 24px;

}

.nav_item_dropdown {

}

.dropdown_container {
  position: relative;
  display: inline-block;
}


.dropdown_container:hover .dropdown_trigger{
  border-radius: 16px 16px 0 0;
  /* background: #cf0000; */
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.15);
}


/* Styling for the trigger div */
.dropdown_trigger {
  color: #000000;
  padding: 0 10px;
  cursor: pointer;
  width: 160px;
  color: #282882;
  font-family: Mont;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 45.5px; /* 227.5% */
  transition: all .2s ease-out;
  position: relative;
  text-decoration: none;
}

.dropdown_trigger:hover {
  border-radius: 16px 16px 0 0;
  background-color: #FFF;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.15);
  position: relative;
}

.dropdown_trigger::after {
  position: absolute;
  content: '';
  width: 20px;
  height: 2px;
  background-color: red;
  bottom: -3px;
  left: 0;
  opacity: 1;
  z-index: 1000000000000000;
  transition: width .2s ease-in;
}

/* 
.link:hover:after {
  opacity: 1;
  width: 24px;

} */


/* Styling for the dropdown content */
.dropdown_content {
  display: none;
  position: absolute;
  background: #fff;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  z-index: 1;
  width: 180px;
  border-radius: 0 0 16px 16px;


}

/* Show the dropdown content when hovering over the container */
.dropdown_container:hover .dropdown_content {
  display: block;
}

/* Style each item in the dropdown */
.dropdown_content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

/* Change color on hover for dropdown items */
.dropdown_content a:hover {
  background-color: #ddd;
}

