.right_nav_container {
  display: flex;
  align-items: center;
  transition: opacity .2s ease-out;
  gap: 30px;
}



.right_nav_image:hover {
  opacity: .65;
}

.right_nav_image {
  width: 32px;
  height: 32px;

}


.curcle {
  background-color: black;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.number_goods {
  font-family: 'Inter';
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  color: #f5f5f5; 
}

.cart_wrapper {
  position: absolute;
  top: 80px;
  right: 30px;
  z-index: 3;
}

.burger_icon {
  min-width: 28px;
  height: 28px;
  border: 0;
  overflow: hidden;
  position: relative;
  background-color: transparent;
  transition: transform, top 0.3s ease 0s;
  display: none;
}


.burger_icon_active {
  background-color: #79FF91;
  border-radius:  50%;
  transition: all .2s ease-in;


}


.burger_span , .burger_icon::before {
  content: "";
  display: block;
  justify-content: center;
  height: 2px;
  width: 60%;
  background-color: rgba(18, 18, 18, 1);
  border-radius: 10px;
  position: absolute;
  left: 4.5px;
  transition: all .3s ease-in;

}

.burger_span {
  bottom: 30%;
}


.burger_icon::before {
  top: 30%;
}

.burger_icon_active .burger_span {
  top: 50%;
  transform: translate(0px, -50%) rotate(45deg);
}

.burger_icon_active::before {
  top: 50%;
  transform: translate(0px, -50%) rotate(135deg)
}


.button_contact {
  font-family: 'Mont';
  font-size: large;
  display: flex;
  width: 213px;
  height: 55px;
  padding: 12px 25px 11.19px 25px;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  border-radius: 46px;
  border: 0;
  color: white;
  background: #292982;  
}


@media (max-width: 920px) {
  .header {
    overflow: hidden;

    height: 70px;
    padding: 14px 0;
    box-sizing: border-box;
    transition: height .4s ease-in-out;
  }

  .header_burger {
    transition: height .4s ease-in-out;
    background-color: #f5f5f5;
    padding: 14px 36px;
    height: 100vh;
    width: 100%;
    position: relative;
    z-index: 100;
    top: 0;

    box-sizing: border-box; 
  }

  .header_wrapper {
    gap: 24px;
    box-sizing:content-box;
  }
  .header_wrapper__body {
    overflow: hidden;
  }

  .nav_list {
    gap: 20px;
    flex-direction: column;
  }

  .link {
    font-size: 24px;
  }

  .burger_icon {
    display: block;
  }
  .nav_container {
    display: none;
  }
  .nav_container_burger {
    position: absolute;
    display: block;
    top: 100px;
  }

  .right_nav_container {
    display: none;
  }

  .header_burger ~ body {
    overflow: hidden;
  }
}